}

img {
  vertical-align: top;
  max-width: 100%;
}

::-ms-clear,
::-ms-reveal {
  display: none;
  width: 0;
  height: 0;
}

input:invalid {
  box-shadow: none;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
