  padding: 5px 0;
  font-size: 21px;
  border-top-width: 1px;
  border-bottom-width: 1px;
}

.title-decor-lg_double span {
  padding: 0;
  border-top-color: #d1ac88;
  border-bottom-color: #d1ac88;
  line-height: 25px;
}

.title-decor-lg_double::before {
  top: -25px;
  background-image: url("../img/sprite.png");
  width: 65px;
  height: 24px;
  background-position: -224px -199px;
