}

.category__item:hover .category__item-img::before,
.category__item:hover .category__item-img::after,
.category__item:hover .category__item-text {
  opacity: 0;
}

.category__item:not(:first-child) {
  margin-top: 40px;
}

.category__item_red .category__item-img::before,
.category__item_beige .category__item-img::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  border-radius: 50%;
  transition: opacity .3s .1s ease-in-out;
}

.category__item_red .category__item-img::before {
  background: rgba(199, 20, 63, 0.9);
}

.category__item_beige .category__item-img::before {
  background: rgba(250, 213, 169, 0.9);
}

.category__item_beige .category__item-text {
  margin-top: 95px;
  color: #7b232c;
}

.category__item-img {
  border-radius: 50%;
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.category__item-img::after {
  position: relative;
  content: '';
  display: block;
  margin: 27px auto 0;
  z-index: 1;
  transition: opacity .3s .1s ease-in-out;
}

.category__item-img_skyline::after {
  background-image: url("../img/sprite.png");
  width: 53px;
  height: 57px;
  background-position: -114px -132px;
}

.category__item-img_brickwall::after {
  background-image: url("../img/sprite.png");
  width: 44px;
  height: 44px;
  background-position: -114px -233px;
}

.category__item-img_loupe::after {
  margin-top: 44px;
  background-image: url("../img/sprite.png");
  width: 37px;
  height: 37px;
  background-position: -217px -233px;
}

.category__item-img_view-arr::after {
  margin-top: 18px;
  background-image: url("../img/sprite.png");
  width: 68px;
  height: 68px;
  background-position: -114px -54px;
}

.category__item-text {
  position: relative;
  display: block;
  margin-top: 85px;
  font-size: 17px;
  line-height: 22px;
  color: #fff;
  z-index: 2;
  font-family: 'Open Sans', Arial, sans-serif;
  font-weight: 400;
  transition: opacity .3s .1s ease-in-out;
}

